import React from 'react';
import PaletteCell from './PaletteCell';
import styled from 'styled-components';

const PaletteBlock = styled.div`
  display: flex;
  margin-top: 8px;
  &:nth-child(odd) {
    margin-left: 16px;
  }
`;

const Palette = ({
  palette,
  selectColorId,
  handleSelectColorCell,
  handleSelectLeftColor,
  handleSelectRightColor,
}) => {
  return (
    <PaletteBlock>
      {palette.colors.map((color, cellIdx) => (
        <PaletteCell
          color={color}
          paletteId={palette.id}
          cellIdx={cellIdx}
          key={`${palette.id}${cellIdx}`}
          selected={
            selectColorId.paletteId === palette.id &&
            selectColorId.colorId === cellIdx
          }
          handleSelectColorCell={handleSelectColorCell}
          handleSelectLeftColor={handleSelectLeftColor}
          handleSelectRightColor={handleSelectRightColor}
        />
      ))}
    </PaletteBlock>
  );
};

export default Palette;
